<template>
    <div>
       <div class="my-5 row justify-content-center">
        <form class="col-md-6" >
            <table class="table">
            <thead>
                <th>品名</th>
                <th>數量</th>
                <th>單價</th>
            </thead>
            <tbody>
                <tr v-for="item in order.products" :key="item.id">
                <td class="align-middle">{{ item.product.title }}</td>
                <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
                <td class="align-middle text-right">{{ item.final_total }}</td>
                </tr>
            </tbody>
            <tfoot>
                <tr>
                <td colspan="2" class="text-right">總計</td>
                <td class="text-right">{{ order.total }}</td>
                </tr>
            </tfoot>
            </table>

            <table class="table">
            <tbody>
                <tr>
                <th width="100">Email</th>
                <td>{{ user.email }}</td>
                </tr>
                <tr>
                <th>姓名</th>
                <td>{{ user.name }}</td>
                </tr>
                <tr>
                <th>收件人電話</th>
                <td>{{ user.tel }}</td>
                </tr>
                <tr>
                <th>收件人地址</th>
                <td>{{ user.address }}</td>
                </tr>
                <tr>
                <th>付款狀態</th>
                <td>
                    <span v-if="!order.is_paid">尚未付款</span>
                    <span v-else class="text-success">付款完成</span>
                </td>
                </tr>
            </tbody>
            </table>
            <div class="text-right" v-if="order.is_paid === false">
            <button class="btn btn-danger" @click.prevent="payOrder">確認付款去</button>
            </div>
        </form>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            orderId: '',
            order: {},
            user:{},
        }
    },
    created(){
        const vm = this;
        vm.orderId = vm.$route.params.orderId;
        vm.getorder();
    },
    methods:{
        getorder(){
            const vm = this;
            const api = `${process.env.API_PATH}${process.env.CUSTOMPATH}/order/${vm.orderId}`;
            this.$http.get(api).then((response) => {
                vm.order = response.data.order;
                vm.user =  response.data.order.user;
            })
        },
        payOrder(){
            const vm = this;
            const api = `${process.env.API_PATH}${process.env.CUSTOMPATH}/pay/${vm.orderId}`;
            this.$http.post(api).then((response) => {
                vm.getorder();
            })
        },
    },
}
</script>


